const rootReducer = combineReducers({
    loginims : loginims,
    Basic: basic,
    Company: company,
    Users: users,
    Products: products,
    Orders: orders,
    media: getMediaReducer,
    jobs: jobs,
    team: team,
    login: login,
    blog: blogsReducer,
    gallary: gallary
});

export default rootReducer