

import React, { Component, useEffect, useState } from 'react';
import { connect } from 'react-redux';
import Loader from '../../components/ui/loader/loader'
import { Layout } from 'antd'
import { withRouter, Redirect } from 'react-router-dom';
import * as actionType from '../../store/actions/action-type'
import Header from '../../components/header/header'
import Layouts from '../../components/layout/layout'
import ContentLayout from '../../components/layout/content-layout'
import Sider from '../../side-nav/sider-nav'
import { tokenVerify } from '../../services/api/login-ims'
import deleteToken from '../../services/api/logout-ims'
import { GetToken, DeleteToken } from '../../helpers/token'

const LoginChecker = (props) => {

  const [contentSlide, setcontentSlide] = useState(false);
  const [showLoader, setshowLoader] = useState(false);
  const [gToken, setgToken] = useState(GetToken());

  const callback = (data) => {
    if (data.status === 200) {
      props.basic(data.data);
    } else if (data.response) {
      props.history.push('/')
      console.log(data.response)
    }
  }

  const logoutCallback = (data) => {
    if (data.status === 200) {
      DeleteToken();
      setshowLoader(false)
      props.isLogout()
      props.history.push('/')
    } else {
      console.log(data.response);
    }
  }

  const logout = () => {
    setshowLoader(true)
    if (gToken) {
      deleteToken(logoutCallback, gToken)
    }
  }

  useEffect(async () => {
    const { currentURL, isLoggedIn, history, setRedirectUrl } = props;

    if (!isLoggedIn) {
      // set the current url/path for future redirection (we use a Redux action)
      // then redirect (we use a React Router method)
      setRedirectUrl(currentURL);
      history.push('/');
    } else if (isLoggedIn && gToken) {
      setRedirectUrl(currentURL);
      tokenVerify(callback, gToken);
    }
  }, [])

  const contentSlider = (data) => {
    setcontentSlide(data)
  }

  const { isLoggedIn, isFirst, isApproved } = props;
  if (isLoggedIn && isFirst && isApproved === 'approved') {
    return (
      <Layout>
        <Header />
        <Layouts>
          <Sider content_slider={contentSlider} roleType={props.userType} logout={() => logout} />
          <ContentLayout contentslide={contentSlide}>
            {showLoader ? <div className="container"><Loader>Processing...</Loader></div> : props.children}
          </ContentLayout>
        </Layouts>
      </Layout>
    )
  } else if (!props.isFirst) {
    return <Redirect to="/user/updatePassword" />
  } else if (props.isApproved === 'pending' || props.isApproved === null) {
    return <Redirect to="/user/profile" />
  }
  // else{
  //   return null
  // }

}

// Grab a reference to the current URL. If this is a web app and you are
// using React Router, you can use `ownProps` to find the URL. Other
// platforms (Native) or routing libraries have similar ways to find
// the current position in the app.
function mapStateToProps(state, ownProps) {
  return {
    isLoggedIn: state.Login.isLogedIn,
    userType: state.Basic.basic.group,
    isFirst: state.Basic.basic.firstTime,
    isApproved: state.Basic.basic.approval,
    currentURL: ownProps.location.pathname
  }
}

function mapDispatchToProps(dispatch) {
  return {
    setRedirectUrl: (data) => {
      dispatch({
        type: actionType.LOGINASIMS,
        value: data
      })
    },
    basic: (data) => {
      dispatch({
        type: actionType.USER_BASIC,
        value: data
      })
    },
    isLogout: () => {
      dispatch({
        type: actionType.LOGOUT
      })
    }
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(LoginChecker))