import React, { useReducer } from 'react';
// import axios from 'axios';
import BlogContext from './blogsContext';
import blogReducer from './blogsReducer';
import axios from '../../services/axios/axios';
import headers from '../../services/axios/headers';

import * as actionType from '../actionTypes';

const BlogsState = props => {

    const initialState = {
        firstRun: true,
        blogItems: [],
        contentOpen: false,
        blogItemsById: []
    };
    const [state, dispatch] = useReducer(blogReducer, initialState)


    // GET MEDIA
    const getBlogs2 = (callback) => {
        axios.get(`/blogs/`, { headers, crossdomain: true })
            .then((res) => {
                callback(res)
                console.log("=====>",res);
            })
            .catch(err => {
                callback({
                    status: 'error',
                    data: err
                });
            });
    };

    const deleteBlog3 = (callback, id) => {
        axios.delete(`/blogs/${id}/`, {
                headers: {
                    ...headers,
                    'Content-Type': 'application/json'
                }
            })
            .then(res => {
                //console.log(res);
                callback(res)
            })
            .catch(err => callback(err))
    };

    const fetchBlogs2 = (data) => {
        dispatch({
            type: actionType.BLOG_ITEM,
            value: data
        })
    }
    const pubunpubBlog2 = (data) => {
        dispatch({
            type: actionType.UNPUBLISHED_BLOG,
            value: data
        });
    }
    const deleteBlog2 = (data) => {
        dispatch({
          type: actionType.DELETE_BLOG,
          value: data
        });
      }
    return <BlogContext.Provider value = {
            {
                firstRun: state.firstRun,
                blogItems: state.blogItems,
                contentOpen: state.contentOpen,
                blogItemsById: state.blogItemsById,
                getBlogs2,
                fetchBlogs2,
                pubunpubBlog2, 
                deleteBlog2,
                deleteBlog3,
                
            }
        } > { props.children } 
        </BlogContext.Provider>

}



export default BlogsState

