import React, { useReducer } from 'react';
// import axios from 'axios';
import MediaContext from './mediaContext';
import mediaReducer from './mediaReducer';
import axios from '../../services/axios/axios';
import headers from '../../services/axios/headers';

import * as actionType from '../actionTypes';

import { initialState } from '../../context/reducer';

const MediaState = props => {
    const initialState = {
        firstRun: true,
        mediaItems: [],
        designfilesItems: []

    };



    const [state, dispatch] = useReducer(mediaReducer, initialState)


    // GET MEDIA
    const getMedia2 = async (callback) => {
        axios.get('/medias/', { headers })
            .then(res => {
                // console.log(res.data);
                callback(res.data);
                console.log('in the action state!!')
            })
            .catch(err => {
                callback({
                    status: 'error',
                    data: err
                });
            });
    };

    const onUpdateMediaItems2 = async (data) => {
        console.log(data)
        dispatch({
            type: actionType.UPDATE_MEDIA_ITEMS,
            value: data
        })
    }

    const onUpdateFirstRun2 = async (data) => {
        console.log(data)

        dispatch({
            type: actionType.UPDATE_FIRST_RUN_MEDIA,
            value: data
        })
    }
    let data = {
        firstRun: state.firstRun,
        mediaItems: state.mediaItems,
        getMedia2,
        onUpdateMediaItems2,
        onUpdateFirstRun2
    }
    return <MediaContext.Provider
        value={data}
    >
        {props.children}
    </MediaContext.Provider>

}



export default MediaState

