import {LOGIN, LOGOUT}  from '../actionTypes'

const initialState = {
    session: !!sessionStorage.jwt
};

const reducer = (state = initialState, action) => {
    switch (action.type){
        case LOGIN:
            return{
                session: !!sessionStorage.jwt,
            }
        case LOGOUT:
            return{
                session: !!sessionStorage.jwt
            }
        default:
            return state
    }
}

export default reducer;

