import * as actionType from  './actionTypes';
import {GetToken} from '../helpers/token'

export const initialState = {
    blogItem : {
        firstRun: true,
        blogItems: [],
        contentOpen: false,
        blogItemsById: []
    },
    gallery : {
        firstRun: true,
        gallaryItems: [],
    },
    jobItem : {
        firstRun: true,
        jobItems: [],
        jobItemsById: []
    },
    login : {
        session: !!sessionStorage.jwt
    },
    loginims : {
        isLogedIn: !!GetToken(),
        redirect: '/dashboard'
    },
    mediaItems : {
        firstRun: true,
        mediaItems: [],
        designfilesItems: []
    },
    teamItems : {
        firstRun: true,
        teamItems: []
    }
}



export default (state = initialState , action) => {
    switch(action.type) {
        case actionType.USER_BASIC:
            return {
                ...state,
                basic: action.value
            }
        case actionType.FIRST_TIME: {
            return {
                ...state,
                basic: action.value
            }
        }
            case actionType.BLOG_ITEM:
            return{
                ...state,
                blogItem:{
                    ...state.blogItem,
                    blogItems: action.value
                } 
            }
            case actionType.BLOG_ITEM_BY_ID:
            return{
              ...state,
              blogItem:{
                    ...state.blogItem,
                    blogItemsById:action.value
              }
            }
            case actionType.ADD_BLOG_ITEM:
            return{
              ...state,
              blogItem:{
                ...state.blogItem,
                blogItems: state.blogItems.concat(action.value)
              }
            }
            case actionType.UPDATE_BLOG_ITEM:{
              return Object.assign({}, state, {
                  blogItems : state.blogItems.map(blog => {
                      if (blog.id !== action.value.id) {
                  return blog;
              }
              return Object.assign({}, blog, {
                  content: action.value.content,
              })
          })
          });
          }
            case actionType.BLOG_CONTENT_OPEN:
            return{
              ...state,
              blogItem : {
                  ...state.blogItem,
                  contentOpen: action.value
              }
            }
            case actionType.UNPUBLISHED_BLOG:{
              return Object.assign({}, state, {
                blogItems : state.blogItems.map(BLOG => {
                      if (BLOG.id !== action.value.id) {
                  return BLOG;
              }
              return Object.assign({}, BLOG, {
                  publish : action.value.publish
              })
          })
          });
          }
          case actionType.DELETE_BLOG:
              return {
                  ...state,
                  blogItem : {
                      ...state.blogItem,
                      blogItems: [...state.blogItems.filter(BLOG => BLOG.id !== action.value)]
                  }
              }
              case actionType.COMPANY:
            return {
                ...state,
                company: action.value
            }
        case actionType.UPDATE_FIRST_RUN_COMPANY:
            return {
                ...state,
                firstRun: action.value
            }
        case actionType.ADD_COMPANY:
            return {
                ...state,
                company: [
                    ...state.company.filter((comp) => comp._id !== action.value._id).concat([action.value])
                ]
            }
        case actionType.ADD_VENDOR_PRODUCTS:
        return Object.assign({}, state, {
            company: state.company.map((vendor) => {
                if(vendor._id !== action.value._id){
                    return vendor
                }

                var newMachine = action.value.products.filter(newMachine =>
                    {
                    return !vendor.products.some(machine => {
                        return newMachine._id === machine._id;
                    });
                });
                
                return Object.assign({}, vendor, {
                    products: vendor.products.concat(newMachine)
                })
            })
        })
        case actionType.IMAGE_GALLARY:
    return{
      ...state,
      gallery : {
            ...state.gallery,
            gallaryItems: action.value
      }
    }
    case actionType.ADD_IMAGE_GALLARY:
    return{
      ...state,
      gallery : {
        ...state.gallery,
        gallaryItems: state.gallaryItems.concat(action.value)
      }
    }
    case actionType.FETCH_JOB:
        return {
            ...state,
            jobItem : {
                ...state.jobItem,
                jobItems: action.value
        }}
    case actionType.FETCH_JOB_BY_ID:
        return {
            ...state,
            jobItem : {
                ...state.jobItem,
            jobItemsById: action.value
            }
        }
    case actionType.CREATE_JOB:
        return {
            ...state,
            jobItem : {
                ...state.jobItem,
            jobItems: state.jobItems.concat(action.value)
            }
        }
    case actionType.UPDATE_JOB:{
        return Object.assign({}, state, {
            jobItems : state.jobItems.map(job => {
                if (job.id !== action.value.id) {
            return job;
        }
        return Object.assign({}, job, {
            position: action.value.position,
            description: action.value.description,
            skills: action.value.skills,
            location: action.value.location,
            qualification: action.value.qualification,
            experience: action.value.experience,
            publish : action.value.publish
        })
    })
    });
    }
    case actionType.UNPUBLISHED_JOB:{
        return Object.assign({}, state, {
            jobItems : state.jobItems.map(job => {
                if (job.id !== action.value.id) {
            return job;
        }
        return Object.assign({}, job, {
            publish : action.value.publish
        })
    })
    });
    }
    case actionType.DELETE_JOB:
        return {
            ...state,
            jobItem : {
                ...state.jobItem,
            jobItems: [...state.jobItems.filter(job => job.id !== action.value)]
            }
        }
        case actionType.LOGIN:
            return{
                session : {
                    ...state.session,
                    session: !!sessionStorage.jwt,
                }
            }
        case actionType.LOGOUT:
            return{
                session : {
                    ...state.session,
                    session: !!sessionStorage.jwt,
                }
            }
            case actionType.LOGINASIMS: 
            return {
                loginims:{
                    ...state.loginims,
                    isLogedIn: !!GetToken(),
                    redirect: action.value
                }
            }
        case actionType.LOGOUTASIMS:
            return {
                loginims:{
                    ...state.loginims,
                    isLogedIn: false
                }
            }
            case actionType.ORDERS: 
            return {
                ...state,
                orders: action.value
            }
        case actionType.UPDATE_FIRST_RUN_ORDERS:
            return {
                ...state,
                firstRun: action.value
            }
        case actionType.ADD_UNPROCEED_ORDER:
            return {
                ...state,
                orders: state.orders.filter((order) => order._id !== action.value._id).concat([action.value])
            }
        case actionType.REMOVE_UNPROCEED_ORDER:
            return {
                ...state,
                orders: state.orders.filter((order) => order._id !== action.value._id)
            }
        case actionType.UNPROCEED_ADD_PRODUCT:
            return Object.assign({}, state, {
                orders: state.orders.map((order) => {

                    if(order._id !== action.value._id){
                        return order
                    }

                    var newProduct = action.value.products.filter(nproduct =>
                        {
                        return !order.products.some(oproduct => {
                            return nproduct._id === oproduct._id;
                        });
                    });

                    return Object.assign({}, order, {
                        products: order.products.concat(newProduct)
                    })
                })
            })
        case actionType.UNPROCEED_REMOVE_PRODUCT:
            return Object.assign({}, state, {
                orders: state.orders.map((order) => {

                    if(order._id !== action.value.data._id){
                        return order
                    }
                    
                    return Object.assign({}, order, {
                        products: order.products.filter((nProduct) => nProduct._id !== action.value.id)
                    })
                })
            })
        case actionType.UPDATE_ORDER_STATUS:
            return Object.assign({}, state, {
                orders: state.orders.map((order) => {
                    if(order._id !== action.value._id){
                        return order
                    }
                    
                    return Object.assign({}, order, {
                        orderStatus: {
                            allocated: action.value.orderStatus.allocated,
                            installed: action.value.orderStatus.installed,
                            packed: action.value.orderStatus.packed,
                            shipped: action.value.orderStatus.shipped
                        }
                    })
                })
            })
            case actionType.PRODUCTS:
                return {
                    ...state,
                    products: action.value
                }
            case actionType.UPDATE_FIRST_RUN_PRODUCTS:
                return {
                    ...state,
                    firstRun: action.value
                }
            case actionType.ADD_PRODUCTS:
                return {
                    ...state,
                    products: state.products.filter((product) => product._id !== action.value._id).concat([action.value])
                }
            case actionType.ADD_VERSION:
                return Object.assign({}, state, {
                    products: state.products.map((product) => {
                        if(product._id !== action.value._id){
                            return product
                        }
    
                        var newDetail = action.value.details.filter(detail =>
                             {
                            return !product.details.some(proDetail => {
                                return detail._id === proDetail._id;
                            });
                        });
                        
                        return Object.assign({}, product, {
                            details: product.details.concat(newDetail)
                        })
                    })
                })
            case actionType.ADD_MACHINE:
                return Object.assign({}, state, {
                    products: state.products.map((product) => {
                        if(product._id !== action.value._id){
                            return product
                        }
    
                        var newMachine = action.value.productList.filter(newMachine =>
                            {
                            return !product.productList.some(machine => {
                                return newMachine._id === machine._id;
                            });
                        });
                        
                        var updatedDetail = action.value.details.filter((detail) => {
                            return detail.version === newMachine[0].version
                        });
                        
                        return Object.assign({}, product, {
                            count: action.value.count,
                            sold: action.value.sold,
                            productList: product.productList.concat(newMachine),
                            details: product.details.map((detail) => {
                                if(detail.version !== newMachine[0].version){
                                    return detail
                                }
    
                                return Object.assign({}, detail, {
                                    count: updatedDetail[0].count
                                })
                            })
                        })
                    })
                })
            case actionType.UPDATE_MACHINE_STATUS:
                return Object.assign({}, state, {
                    products: state.products.map((product) => {
                        if(product._id !== action.value.data._id){
                            return product
                        }
    
                        var updatedStatus = action.value.data.productList.filter((machine) => {
                            return machine.serialNumber === action.value.serialNumber
                        });
    
                        return Object.assign({}, product, {
                            count: action.value.count,
                            sold: action.value.sold,
                            productList: product.productList.map((machine) => {
                                if(machine.serialNumber !== action.value.serialNumber){
                                    return machine
                                }
                                
                                return Object.assign({}, machine, {
                                    sold: updatedStatus[0].sold
                                })
                            })
                        })
                    })
                })
            case actionType.UPDATE_FIRST_RUN_TEAM:
      return {
        ...state,
        teamItems : {
            ...state.teamItems,
            firstRun: action.value
        }
      };
      

    case actionType.UPDATE_TEAM_ITEMS:
      return {
        ...state,
        teamItems : {
            ...state.teamItems,
        teamItems: state.teamItems.concat(action.value)
        }
      };
      case actionType.USERS:
            return {
                ...state,
                users: action.value
            }
        case actionType.UPDATE_FIRST_RUN_USERS:
            return {
                ...state,
                firstRun: action.value
            }
        case actionType.ADD_USER:
            return {
                ...state,
                users: [
                    ...state.users.filter((user) => user._id !== action.value._id).concat([action.value])
                ]
            }
        case actionType.APPROVE_USER:
            return Object.assign({}, state, {
                users: state.users.map((user) => {
                    if(user._id !== action.value._id){
                        return user
                    }

                    return Object.assign({}, user, {
                        approval: action.value.approval
                    })
                })
            })
        case actionType.USER_PROFILE:
            return {
                ...state,
                profile: action.value
            }
        case actionType.USER_COMPANY:
            return {
                ...state,
                company: action.value
            }
        default:
            return state
    }
}

