import React from 'react';
import ReactDOM from 'react-dom';
import { HashRouter } from 'react-router-dom';

// importing the context provider
import {AppProvider} from './context/store';

// importing all the Redux related dependencies
import { createStore, applyMiddleware, compose, combineReducers } from 'redux';
import { Provider } from 'react-redux';
import thunk from 'redux-thunk';

// imporing the Reducers for the application

import App from './app.jsx';
import './styles/index.css';
import './index.css';    
import rootReducer from './store/reducer'
import registerServiceWorker from './registerServiceWorker';
import AuthState from './context2/authContext/authState';
import MediaState from './context2/mediaContext/mediaState'
import BlogsState from './context2/blogsContext/blogsState'
import store  from './store'

// Adding the Redux Dev Tool setup
const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const middleware = [thunk];
 
// Creating store for the application
const store2 = createStore(rootReducer, composeEnhancers(applyMiddleware(...middleware)));

ReactDOM.render(
  // <Provider store={store}>
  <AuthState>
    <MediaState>
    <BlogsState>
      
    <Provider store={store2}>
      
      <HashRouter basename='/'>
        <App />
      </HashRouter>
    </Provider>
    </BlogsState>
      
   </MediaState>
   </AuthState>,
  // </Provider>,
  document.getElementById('root')
);

registerServiceWorker(); 
